       .userProfile{
        width:var(--profilew);
        height:var(--profilew);
        padding:10px;
        border-radius: 50%;
        background: linear-gradient(135deg, rgb(224, 215, 215),rgb(113, 110, 110) 50%,transparent 50%);
        background-size: 250%;
        background-position: 100% 100% !important;
        z-index: 2;
        transition-property: background,transform !important;
        transition : background 0.7s;
};
.userProfile [data-aos^=flip][data-aos^=flip]{
        transition-property: background,tranform;
}
.userProfile:hover{
background-position: 0% 0% !important;
z-index: 2;
}
