#main{
    border: 1px solid red;
 
}
#home{
    background-color: var(--bg);
    padding: 0 var(--pad1); 
    display: flex;
}
.greet{
    border: 1px solid green;
    line-height: 3rem;
    width: 65rem;

}
.greet h2{
        font-size: 3.4rem;
        letter-spacing: .15rem;
}
.greet p{
        font-size: 3.3rem;
        font-family: var(--fontf1);
        color: var(--secondary-color);
}
.greet h1{
    font-size: 5rem;   
    letter-spacing: 0.2rem;
    font-family: var(--fontf1);
    color: var(--secondary-color);
}

.greet h1 span{
    color: var(--primary-color);
}
.hand{
    animation:handshake 2.5s infinite linear ;
    transform-origin: 70% 70%; 
    display: inline-block;
};
@keyframes handshake {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    10% {
        -webkit-transform: rotate(14deg);
        transform: rotate(14deg);
    }
    20% {
        -webkit-transform: rotate(-8deg);
        transform: rotate(-8deg);
    }
    30% {
        -webkit-transform: rotate(14deg);
        transform: rotate(14deg);
    }
    40% {
        -webkit-transform: rotate(-4deg);
        transform: rotate(-4deg);
    }
    50% {
        -webkit-transform: rotate(10deg);
        transform: rotate(10deg);
    }
    60% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
}
.follow{
    display: flex;
    gap: 1rem;
    margin-bottom: 10px;
}
.follow svg{
    width: 2rem;
    cursor: pointer;
    height: 1.8rem;
      transition: width .1s ease-in-out, height .1s ease-in-out;
};
.follow svg:hover{
    width: 2.2rem;
    height: 2rem;
}
.follow .git{
    fill: #2b221c;
    transition: width .1s ease-in-out, height .1s ease-in-out;
}

.follow .linked{
    fill: 
    #0a66c2 ;
      transition: width .1s ease-in-out, height .1s ease-in-out;
}

.icons1 {
    width: 3rem;
    height: 2rem;
}
#buttons{
    display: flex;
    gap: 1rem;
        margin: 2rem 0;
}
#resume,.hire{
    background-color: var(--primary-color);
    border: 1.5px solid var(--primary-color);
    color: #fff;
    font-size: var(--font1);
    cursor:pointer;
    border-radius: var(--radius);
    transition: background-color .2s ease-in-out, color .2s ease-in-out;
}
#resume{
    padding: 0.75rem 1.8rem !important;
}
.hire{
    padding: 0.75rem 2.5rem;
}
/* Hover Transitions */
.follow .linked:hover{
    width: 2.2rem;
    height: 2rem;
}
.follow  .git:hover{
    width: 2.2rem;
    height: 2rem;
}
#resume:hover,.hire:hover{
    color: var(--primary-color);
    background-color: white;
}
.dev img{
    width: 70%;
}
.dev{
    display: flex;
    align-items: center;
    justify-content: end;

}
#about{
    padding: 2.5rem var(--pad1);
}
#about>#container{
    border: 1px solid green;
    display: flex;
    justify-content: space-evenly;
}
.header{
    text-align: center;
    color:var(--primary-color);
    position: relative;
}
.header::before{
    content: "";
    background: var(--secondary-color);
    position: absolute;
    top: 2.7rem;
    width: 9.2rem;
    height: 0.2rem;
    border-radius: var(--radius);
}; 
 

